body {
  color: #fff;
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 1rem; }

button {
  background: none;
  border: none; }

.page-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
  z-index: 99;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.5s, opacity 0.5s;
  pointer-events: none; }

.page-overlay--onload {
  visibility: visible;
  opacity: 1; }

.page-overlay--visible {
  visibility: visible;
  opacity: 1;
  transition: visibility 0s, opacity 0.5s; }

